header {
  background-color: #409eff;
  color: white;
  height: 4.4rem;
  border-bottom: 1px solid #ebeef5;
  width: 100vw;
  .nav-tips {
    font-size: 1.8rem;
    line-height: 4.4rem;
    padding-left: 1.5rem;
  }
  .user-info {
    .user-avater {
      margin-right: 0.9375rem;
      height: 3rem;
      width: 3rem;
      vertical-align: middle;
    }
    padding: 0.8rem 5rem 1.25rem 1rem;
    font-size: 1.3rem;
    height: 100%;
    float: right;
    cursor: pointer;
  }
}
.menu-item {
  width: 10.5rem;
  height: 3.125rem;
  font-size: 1.125rem;
  padding-left: 0.625rem;
}
.index-mainer {
  background-color: #ededf3;
  height: 90vh;
  width: 100vw;
  position: relative;
  overflow-y: scroll;
  aside {
    background-color: rgb(255, 255, 255);
    left: 0;
    top: 0;
    position: absolute;
    width: 14vw;
    border: 1px solid #ebeef5;
    height: 100%;
    .nav-menu {
      font-size: 1.25rem;
      color: red;
    }
    .sub-Menu {
      vertical-align: middle;
      margin-top: 40px;
      font-size: 1.2rem;
      .sub-item {
        height: 3.125rem;
        line-height: 3.125rem;
        font-size: 1rem;
        text-indent: 1.25rem;
      }
    }
  }
  .index-container {
    background-color: rgb(255, 255, 255);
    margin: 10px 1rem;
    border: 1px solid rgb(238, 234, 234);
    .heander-Tab {
      height: 40px;
      width: 100%;
    }
    .route-mainer {
      margin-top: -40px;
      height: 100%;
      overflow-y: auto;
      background-color: white;
      .route-container {
        height: calc(100% - 40px);
        margin-top: 40px;
      }
    }
    height: 92%;
    position: absolute;
    left: 14vw;
    width: 83%;
  }
}
